.search-form {
  display: flex;
  width: 100%;
  max-width: 1200px;
  padding: 0 10px;
  margin: 20px auto;
  box-sizing: border-box;
}

.form-input,
.form-submit {
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  color: #495057;
  border-radius: 0.25rem;
  outline: none;
}

.form-input {
  border: 1px solid #ced4da;
  flex: 1 1 auto;
}
