.search-result {
  max-width: 1200px;
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-between; */
}

.serp-item {
  /* display: inline-block; */
  flex: 1;
  min-width: 200px;
  max-width: 300px;
  max-height: 200px;
  /* width: 200px; */
  /* height: 200px; */
  margin: 10px;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.image {
  min-width: 100%;
  min-height: 100%;
  opacity: 0;
}

.loader {
  height: 50px;
  margin-top: 50px;
}

.loader {
  animation: loader-spin infinite 1s linear;
}

@keyframes loader-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
